let callBackList = {}
class TopBarHandler
{
    addSubscriber(key, value){
        callBackList[key] = value
    }

    runCallbackFrom(key, params){
        if(typeof callBackList[key] !== 'undefined'){
            callBackList[key](params)
        }
    }

    filterGroup(target){
        let group = target.getAttribute('group')
        if(group !== null){
            let elements = document.querySelectorAll(`[group=${group}]`)
            elements.forEach(function(userItem) {
                userItem.style.border = '1px solid gray';
            })
            target.style.border = '1px solid black';
        }   
    }

    toggle(target){
        let boolean = target.getAttribute('toggle') === 'true'
        
        if(boolean){
            let selected = target.getAttribute('toggle-selected')
            if( selected === 'false'){
                target.setAttribute('toggle-selected', 'true')
                target.style.backgroundColor = 'buttonface';
                target.style.color = 'gray';
            } else {
                target.setAttribute('toggle-selected', 'false')
                target.style.backgroundColor = 'gray';
                target.style.color = 'buttonface';
            }
        }
    }
}

const topBar = new TopBarHandler()
export default topBar;